/**
 * Chat data uploader
 *
 * Uploads chat sessions and messages from queue to Supabase
 */

import type { SupabaseClient } from "@supabase/supabase-js";
import type { Database } from "@zest/types/database.types";
import { getValidSession } from "../auth/session-manager.js";
import {
  MESSAGES_QUEUE_FILE,
  MIN_MESSAGES_PER_SESSION,
  PLATFORM,
  SESSIONS_QUEUE_FILE,
  SOURCE,
  STALE_SESSION_AGE_MS,
} from "../config/constants.js";
import type {
  ChatMessageInsert,
  ChatSessionInsert,
  ClaudeExtractedMessage,
  ClaudeExtractedSession,
} from "../types/index.js";
import { logger } from "../utils/logger.js";
import { atomicUpdateQueue, clearQueue, readQueue } from "../utils/queue-manager.js";
import { normalizeSessionId } from "../utils/session-id-normalizer.js";

interface SessionCategories {
  valid: ClaudeExtractedSession[];
  stale: ClaudeExtractedSession[];
  pending: ClaudeExtractedSession[];
  validIds: Set<string>;
  staleIds: Set<string>;
  pendingIds: Set<string>;
}

interface MessagePartition {
  valid: ClaudeExtractedMessage[];
  stale: ClaudeExtractedMessage[];
  pending: ClaudeExtractedMessage[];
  orphaned: ClaudeExtractedMessage[]; // New messages for sessions already in DB
}

/**
 * Count messages per session
 */
function countMessagesPerSession(messages: ClaudeExtractedMessage[]): Map<string, number> {
  const counts = new Map<string, number>();
  for (const message of messages) {
    if (!message.session_id) continue;
    const count = counts.get(message.session_id) || 0;
    counts.set(message.session_id, count + 1);
  }
  return counts;
}

/**
 * Categorize sessions into valid, stale, and pending based on message count and age thresholds
 */
function categorizeSessions(
  sessions: ClaudeExtractedSession[],
  messageCountBySession: Map<string, number>,
): SessionCategories {
  const now = Date.now();
  const staleThreshold = now - STALE_SESSION_AGE_MS;

  const valid: ClaudeExtractedSession[] = [];
  const stale: ClaudeExtractedSession[] = [];
  const pending: ClaudeExtractedSession[] = [];

  for (const session of sessions) {
    if (!session.id) continue;

    const messageCount = messageCountBySession.get(session.id) || 0;
    const sessionAge = session.created_at ? new Date(session.created_at).getTime() : now;

    if (messageCount >= MIN_MESSAGES_PER_SESSION) {
      valid.push(session);
    } else if (sessionAge < staleThreshold) {
      stale.push(session);
    } else {
      pending.push(session);
    }
  }

  return {
    valid,
    stale,
    pending,
    validIds: new Set(valid.map((s) => s.id).filter((id): id is string => !!id)),
    staleIds: new Set(stale.map((s) => s.id).filter((id): id is string => !!id)),
    pendingIds: new Set(pending.map((s) => s.id).filter((id): id is string => !!id)),
  };
}

/**
 * Partition messages based on their session category
 * Orphaned messages = new messages for sessions that were already uploaded to DB
 */
function partitionMessagesBySessionCategory(
  messages: ClaudeExtractedMessage[],
  categories: SessionCategories,
): MessagePartition {
  const allSessionIds = new Set([
    ...categories.validIds,
    ...categories.staleIds,
    ...categories.pendingIds,
  ]);

  const orphaned: ClaudeExtractedMessage[] = [];
  const valid: ClaudeExtractedMessage[] = [];
  const stale: ClaudeExtractedMessage[] = [];
  const pending: ClaudeExtractedMessage[] = [];

  for (const message of messages) {
    if (!message.session_id) continue;

    if (categories.validIds.has(message.session_id)) {
      valid.push(message);
    } else if (categories.staleIds.has(message.session_id)) {
      stale.push(message);
    } else if (categories.pendingIds.has(message.session_id)) {
      pending.push(message);
    } else {
      // Session not in queue -> might be in DB (already uploaded) or might not exist
      // We'll validate against DB later to determine which messages are truly valid
      orphaned.push(message);
    }
  }

  return { valid, stale, pending, orphaned };
}

/**
 * Log session categorization for debugging
 */
function logSessionCategorization(
  categories: SessionCategories,
  messagePartition: MessagePartition,
): void {
  if (categories.stale.length > 0) {
    logger.info(
      `Removing ${categories.stale.length} stale sessions (< ${MIN_MESSAGES_PER_SESSION} messages, > 7 days old) with ${messagePartition.stale.length} messages`,
    );
  }
  if (categories.pending.length > 0) {
    logger.info(
      `Keeping ${categories.pending.length} pending sessions (< ${MIN_MESSAGES_PER_SESSION} messages, within 7 days) with ${messagePartition.pending.length} messages`,
    );
  }
}

/**
 * Atomically remove stale sessions from queue
 */
async function removeStaleSessionsFromQueue(staleSessionIds: Set<string>): Promise<void> {
  await atomicUpdateQueue<ClaudeExtractedSession>(SESSIONS_QUEUE_FILE, (currentSessions) => {
    return currentSessions.filter((s) => s.id && !staleSessionIds.has(s.id));
  });

  await atomicUpdateQueue<ClaudeExtractedMessage>(MESSAGES_QUEUE_FILE, (currentMessages) => {
    return currentMessages.filter((m) => m.session_id && !staleSessionIds.has(m.session_id));
  });
}

/**
 * Deduplicate sessions by ID, keeping the most recent version
 */
function deduplicateSessions(sessions: ClaudeExtractedSession[]): ClaudeExtractedSession[] {
  const sessionMap = new Map<string, ClaudeExtractedSession>();

  for (const session of sessions) {
    if (!session.id) continue;

    const existing = sessionMap.get(session.id);
    if (!existing) {
      sessionMap.set(session.id, session);
      continue;
    }

    // Keep the session with the most recent created_at timestamp
    const existingTime = existing.created_at ? new Date(existing.created_at).getTime() : 0;
    const currentTime = session.created_at ? new Date(session.created_at).getTime() : 0;

    if (currentTime >= existingTime) {
      sessionMap.set(session.id, session);
    }
  }

  return Array.from(sessionMap.values());
}

/**
 * Deduplicate messages by session_id + message_index, keeping the most recent version
 */
function deduplicateMessages(messages: ClaudeExtractedMessage[]): ClaudeExtractedMessage[] {
  const messageMap = new Map<string, ClaudeExtractedMessage>();

  for (const message of messages) {
    if (!message.session_id) continue;

    const key = `${message.session_id}:${message.message_index}`;
    const existing = messageMap.get(key);

    if (!existing) {
      messageMap.set(key, message);
      continue;
    }

    // Keep the message with the most recent created_at timestamp
    const existingTime = existing.created_at ? new Date(existing.created_at).getTime() : 0;
    const currentTime = message.created_at ? new Date(message.created_at).getTime() : 0;

    if (currentTime >= existingTime) {
      messageMap.set(key, message);
    }
  }

  return Array.from(messageMap.values());
}

/**
 * Enrich extracted sessions with user and platform metadata for database insertion
 * Normalizes non-UUID session IDs to valid UUIDs at the database boundary
 */
function enrichSessionsForUpload(
  sessions: ClaudeExtractedSession[],
  userId: string,
  workspaceId: string | null,
): ChatSessionInsert[] {
  const filteredSessions = sessions.filter((s) => s.id); // Only process sessions with valid IDs
  
  if (filteredSessions.length < sessions.length) {
    logger.warn(
      `Filtered out ${sessions.length - filteredSessions.length} sessions without IDs`,
    );
  }
  
  return filteredSessions.map((s) => {
    return {
      ...s,
      id: normalizeSessionId(s.id!), // Normalize session ID at DB boundary
      user_id: userId,
      platform: PLATFORM,
      source: SOURCE,
      analysis_status: "pending" as const,
      workspace_id: workspaceId,
      metadata: null,
    };
  });
}

/**
 * Enrich extracted messages with user metadata for database insertion
 * Normalizes non-UUID session IDs to valid UUIDs at the database boundary
 */
function enrichMessagesForUpload(
  messages: ClaudeExtractedMessage[],
  userId: string,
): ChatMessageInsert[] {
  const filteredMessages = messages.filter((m) => m.session_id); // Only process messages with valid session IDs
  
  if (filteredMessages.length < messages.length) {
    logger.warn(
      `Filtered out ${messages.length - filteredMessages.length} messages without session IDs`,
    );
  }
  
  return filteredMessages.map((m) => ({
    ...m,
    session_id: normalizeSessionId(m.session_id!), // Normalize session ID at DB boundary
    user_id: userId,
    code_diffs: null, // Messages don't have code diffs (events do)
    metadata: null,
  }));
}

/**
 * Upload sessions to Supabase
 */
async function uploadSessionsToSupabase(
  supabase: SupabaseClient<Database>,
  sessions: ChatSessionInsert[],
): Promise<boolean> {
  if (sessions.length === 0) {
    return true;
  }

  const { error } = await supabase.from("chat_sessions").upsert(sessions, { onConflict: "id" });

  if (error) {
    logger.error("Failed to upload chat sessions", error);
    return false;
  }

  logger.info(`✓ Uploaded ${sessions.length} chat sessions`);
  return true;
}

/**
 * Upload messages to Supabase
 */
async function uploadMessagesToSupabase(
  supabase: SupabaseClient<Database>,
  messages: ChatMessageInsert[],
): Promise<boolean> {
  if (messages.length === 0) {
    return true;
  }

  const { error } = await supabase
    .from("chat_messages")
    .upsert(messages, { onConflict: "session_id,message_index" });

  if (error) {
    logger.error("Failed to upload chat messages", error);
    return false;
  }

  logger.info(`✓ Uploaded ${messages.length} chat messages`);
  return true;
}

/**
 * Atomically remove processed (uploaded + stale) sessions from queue
 */
async function removeProcessedSessionsFromQueue(sessionsToRemove: Set<string>): Promise<void> {
  await atomicUpdateQueue<ClaudeExtractedSession>(SESSIONS_QUEUE_FILE, (currentSessions) => {
    return currentSessions.filter((s) => s.id && !sessionsToRemove.has(s.id));
  });

  await atomicUpdateQueue<ClaudeExtractedMessage>(MESSAGES_QUEUE_FILE, (currentMessages) => {
    return currentMessages.filter((m) => m.session_id && !sessionsToRemove.has(m.session_id));
  });
}

/**
 * Atomically remove specific messages from queue (for orphaned messages)
 */
async function removeMessagesFromQueue(messageIdsToRemove: Set<string>): Promise<void> {
  await atomicUpdateQueue<ClaudeExtractedMessage>(MESSAGES_QUEUE_FILE, (currentMessages) => {
    return currentMessages.filter((m) => m.id && !messageIdsToRemove.has(m.id));
  });
}

/**
 * Upload all queued chat data (sessions and messages) to Supabase
 */
export async function uploadChatData(
  supabase: SupabaseClient<Database>,
): Promise<{ success: boolean; uploaded: { sessions: number; messages: number } }> {
  try {
    // 1. Check authentication
    const session = await getValidSession();
    if (!session) {
      logger.debug("Not authenticated, skipping chat upload");
      return { success: false, uploaded: { sessions: 0, messages: 0 } };
    }

    // 2. Read queued data
    const queuedSessions = await readQueue<ClaudeExtractedSession>(SESSIONS_QUEUE_FILE);
    const queuedMessages = await readQueue<ClaudeExtractedMessage>(MESSAGES_QUEUE_FILE);

    if (queuedSessions.length === 0 && queuedMessages.length === 0) {
      logger.debug("No chat data to upload");
      return { success: true, uploaded: { sessions: 0, messages: 0 } };
    }

    // 3. Categorize sessions by quality
    const messageCountBySession = countMessagesPerSession(queuedMessages);
    const categories = categorizeSessions(queuedSessions, messageCountBySession);
    const messagePartition = partitionMessagesBySessionCategory(queuedMessages, categories);

    // 4. Log categorization
    logSessionCategorization(categories, messagePartition);

    // 5. Validate orphaned messages (messages whose sessions aren't in queue)
    // Orphaned messages might have sessions in DB OR might be invalid (session never extracted/failed)
    // We need to verify which sessions actually exist in DB before uploading
    if (messagePartition.orphaned.length > 0) {
      // Get unique session IDs from orphaned messages (normalized for DB lookup)
      const orphanedSessionIds = [
        ...new Set(
          messagePartition.orphaned
            .map((m) => m.session_id)
            .filter((id): id is string => !!id)
            .map((id) => normalizeSessionId(id)),
        ),
      ];
      
      // Query database to find which sessions actually exist
      const { data: existingSessions, error: queryError } = await supabase
        .from("chat_sessions")
        .select("id")
        .in("id", orphanedSessionIds);
      
      if (queryError) {
        // On query error, skip all orphaned messages to be safe
        const orphanedMessageIds = new Set(
          messagePartition.orphaned.map((m) => m.id).filter((id): id is string => !!id),
        );
        await removeMessagesFromQueue(orphanedMessageIds);
        messagePartition.orphaned = [];
      } else {
        const existingSessionIds = new Set(existingSessions?.map((s) => s.id) || []);
        
        // Split orphaned messages into valid (session exists) and invalid (session doesn't exist)
        const validOrphaned: ClaudeExtractedMessage[] = [];
        const invalidOrphaned: ClaudeExtractedMessage[] = [];
        
        for (const message of messagePartition.orphaned) {
          if (!message.session_id) {
            invalidOrphaned.push(message);
            continue;
          }
          
          const normalizedSessionId = normalizeSessionId(message.session_id);
          if (existingSessionIds.has(normalizedSessionId)) {
            validOrphaned.push(message);
          } else {
            invalidOrphaned.push(message);
          }
        }
        
        // Remove invalid orphaned messages from queue (their sessions don't exist anywhere)
        if (invalidOrphaned.length > 0) {
          const invalidMessageIds = new Set(
            invalidOrphaned.map((m) => m.id).filter((id): id is string => !!id),
          );
          await removeMessagesFromQueue(invalidMessageIds);
        }
        
        // Keep only valid orphaned messages for upload
        messagePartition.orphaned = validOrphaned;
      }
    }

    // 6. Clean up stale sessions
    if (categories.stale.length > 0) {
      await removeStaleSessionsFromQueue(categories.staleIds);
    }

    // 7. If no valid sessions and no orphaned messages, exit
    if (categories.valid.length === 0 && messagePartition.orphaned.length === 0) {
      return { success: true, uploaded: { sessions: 0, messages: 0 } };
    }

    // 8. Deduplicate data (queue might contain duplicate entries from multiple extractions)
    const uniqueSessions = deduplicateSessions(categories.valid);
    const allMessagesToUpload = [...messagePartition.valid, ...messagePartition.orphaned];
    const uniqueMessages = deduplicateMessages(allMessagesToUpload);

    // 9. Enrich data for upload
    const sessionsToUpload = enrichSessionsForUpload(
      uniqueSessions,
      session.userId,
      session.workspaceId || null,
    );
    
    // Get the set of session IDs that will be uploaded now
    const uploadedSessionIds = new Set(sessionsToUpload.map((s) => s.id));
    
    // Get the set of session IDs that are already in DB (validated orphaned messages)
    const orphanedSessionIds = new Set(
      messagePartition.orphaned
        .map((m) => normalizeSessionId(m.session_id!))
        .filter((id): id is string => !!id),
    );
    
    // Combined set: sessions being uploaded + sessions already in DB
    const allValidSessionIds = new Set([...uploadedSessionIds, ...orphanedSessionIds]);
    
    // Only upload messages whose normalized session_id exists in either set
    const messagesToUpload = enrichMessagesForUpload(uniqueMessages, session.userId).filter(
      (m) => allValidSessionIds.has(m.session_id),
    );
    
    // Verify: All messages reference sessions that exist (either being uploaded or already in DB)
    const messageSessionIds = new Set(messagesToUpload.map((m) => m.session_id));
    const missingSessionIds = [...messageSessionIds].filter((id) => !allValidSessionIds.has(id));
    
    if (missingSessionIds.length > 0) {
      return { success: false, uploaded: { sessions: 0, messages: 0 } };
    }

    // 8. Upload to Supabase (sessions first, then messages)
    const sessionsUploaded = await uploadSessionsToSupabase(supabase, sessionsToUpload);
    if (!sessionsUploaded) {
      return { success: false, uploaded: { sessions: 0, messages: 0 } };
    }

    const messagesUploaded = await uploadMessagesToSupabase(supabase, messagesToUpload);
    if (!messagesUploaded) {
      return {
        success: false,
        uploaded: { sessions: sessionsToUpload.length, messages: 0 },
      };
    }

    // 10. Clean up queue: remove uploaded sessions and their messages
    const sessionsToRemove = new Set([...categories.validIds, ...categories.staleIds]);
    await removeProcessedSessionsFromQueue(sessionsToRemove);

    // 11. Remove validated orphaned messages after successful upload
    // These are messages whose sessions existed in DB (validated in step 5) and were successfully uploaded
    if (messagePartition.orphaned.length > 0) {
      const orphanedMessageIds = new Set(
        messagePartition.orphaned.map((m) => m.id).filter((id): id is string => !!id),
      );
      await removeMessagesFromQueue(orphanedMessageIds);
    }

    return {
      success: true,
      uploaded: {
        sessions: sessionsToUpload.length,
        messages: messagesToUpload.length,
      },
    };
  } catch (error) {
    logger.error("Failed to upload chat data", error);
    return { success: false, uploaded: { sessions: 0, messages: 0 } };
  }
}

/**
 * Upload chat data with retry logic
 */
export async function uploadChatDataWithRetry(
  supabase: SupabaseClient<Database>,
  maxRetries = 3,
  backoffMs = 5000,
): Promise<{ success: boolean; uploaded: { sessions: number; messages: number } }> {
  let lastError: Error | null = null;

  for (let attempt = 1; attempt <= maxRetries; attempt++) {
    try {
      const result = await uploadChatData(supabase);
      if (result.success) {
        return result;
      }

      // If not successful but no exception thrown, don't retry (likely auth issue)
      return result;
    } catch (error) {
      lastError = error as Error;
      logger.warn(`Chat upload attempt ${attempt}/${maxRetries} failed: ${lastError.message}`);

      if (attempt < maxRetries) {
        const delay = backoffMs * attempt; // Exponential backoff
        logger.debug(`Retrying in ${delay}ms...`);
        await new Promise((resolve) => setTimeout(resolve, delay));
      }
    }
  }

  logger.error(`Chat upload failed after ${maxRetries} attempts`, lastError);
  return { success: false, uploaded: { sessions: 0, messages: 0 } };
}
